import React, { useContext, useRef, useState } from "react";
import "./contact.scss";
import emailjs from "emailjs-com";
import { ThemeContext } from "../../context";

export default function Contact() {
  const formRef = useRef();
  const [done, setDone] = useState(false);
  const theme = useContext(ThemeContext);
  const darkMode = theme.state.darkMode;

  const handleSubmit = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_bjgwr9a",
        "template_due5k8o",
        formRef.current,
        "9eClLuGjVMYI51fQ_"
      )
      .then(
        (result) => {
          console.log(result.text);
          setDone(true);
        },
        (error) => {
          console.log(error.text);
        }
      );
  };

  return (
    <div className="c">
      <div className="c-bg"></div>
      <div className="c-wrapper">
        <div className="c-left">
          <h1 className="c-title">Lorem ipsum dolor sit.</h1>
          <div className="c-info">
            <div className="info-item">
              <img
                src="assets/email.png"
                alt=""
                srcset=""
                className="info-icon"
              />
              paulsavoiu747@gmail.com
            </div>
            <div className="info-item">
              <img
                src="assets/phone.png"
                alt=""
                srcset=""
                className="info-icon"
              />
              +40731811431
            </div>
          </div>
        </div>
        <div className="c-right">
          <p className="c-desc">
            <b>Lorem, ipsum dolor.</b> Lorem ipsum dolor sit amet consectetur
            adipisicing elit. Saepe magni similique ipsum, aliquid eaque
            obcaecati odit architecto non nam magnam!
          </p>
          <form className="right-form" ref={formRef} onSubmit={handleSubmit}>
            <input
              style={{
                backgroundColor: darkMode && "#333",
                color: darkMode && "#fff",
              }}
              type="text"
              placeholder="Name"
              name="user_name"
            />
            <input
              style={{
                backgroundColor: darkMode && "#333",
                color: darkMode && "#fff",
              }}
              type="text"
              placeholder="Subject"
              name="user_subject"
            />
            <input
              style={{
                backgroundColor: darkMode && "#333",
                color: darkMode && "#fff",
              }}
              type="text"
              placeholder="Email"
              name="user_email"
            />
            <textarea
              style={{
                backgroundColor: darkMode && "#333",
                color: darkMode && "#fff",
              }}
              id=""
              rows="5"
              placeholder="Message"
              name="message"
            ></textarea>
            <button type="submit">Submit</button>
            {done && "Thank you!"}
          </form>
        </div>
      </div>
    </div>
  );
}
