import React, { useContext } from "react";
import { ThemeContext } from "../../context";
import "./about.scss";

export default function About() {
  const theme = useContext(ThemeContext);
  const darkMode = theme.state.darkMode;

  return (
    <div className="about">
      <div className="a-left">
        <div
          className="a-card bg"
          style={{
            backgroundColor: darkMode ? "#fff" : "#333",
            transition: "all 1s ease",
          }}
        ></div>
        <div className="a-card">
          <img src="assets/olteanu.jpg" alt="" className="img" />
        </div>
      </div>
      <div className="a-right">
        <h1 className="right-title">About Me</h1>
        <p className="right-sub">
          Lorem ipsum dolor sit, amet consectetur adipisicing elit. Dicta, ut
          minima dolores at esse blanditiis!
        </p>
        <p className="right-desc">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Nostrum
          doloremque a voluptatibus maiores laboriosam in, sint pariatur quo
          impedit reiciendis deleniti molestiae dolorem harum quisquam cumque
          praesentium obcaecati voluptatem veniam error eius nulla magnam sequi.
          Magni, maxime necessitatibus quam enim neque rerum! Reprehenderit
          cupiditate asperiores animi excepturi eveniet quos amet.
        </p>
      </div>
    </div>
  );
}
